@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  font-family: 'Lato';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.noScroll::-webkit-scrollbar {
  display: none;
}

.noScroll {
-ms-overflow-style: none;
scrollbar-width: none;
}

::-webkit-scrollbar {
  width: 7px;
}


@layer components {
  ::-webkit-scrollbar-track {
    @apply shadow-inner;
  }
  
  ::-webkit-scrollbar-thumb {
    @apply bg-primary-500;
  }
  
  ::-webkit-scrollbar-thumb:hover {
    @apply bg-primary-700;
  }
  table{
    @apply w-full
  }
  th {
    @apply border-2 border-primary-500 text-left text-white bg-primary-500  ;
  }
  td{
    @apply border-2 border-primary-500 text-sm max-h-10  overflow-clip;
  }
}